<template>
  <q-page class="fit row justify-center items-center bg-secondary fullscreen">
    <!-- <particles-bg type="cobweb" :bg="true"  num="20"   alpha=" [0.1, 0]"/> -->
    <div class="column">
      <div class="row">
        <q-intersection transition="flip-left" v-show="loginView">
          <login v-if="loginView" @change="changeCard" />
        </q-intersection>
        <q-intersection transition="flip-right" v-show="registerView">
          <register-account v-if="registerView" @change="changeCard" />
        </q-intersection>
        <q-intersection transition="flip-right" v-show="forgotPasswordView">
          <forgot-password v-if="forgotPasswordView" @change="changeCard" />
        </q-intersection>
      </div>
       <div class="text-center text-caption text-white q-pt-sm">© 2021 roostec</div>
    </div>
  </q-page>
</template>

<script>
import Login from "@/components/authentication/Login.vue";
import RegisterAccount from "@/components/authentication/RegisterAccount.vue";
import ForgotPassword from "@/components/authentication/ForgotPassword.vue";
// import { ParticlesBg } from "particles-bg-vue";
export default {
  name: "Auth",
  components: {
    RegisterAccount,
    Login,
    ForgotPassword,
    // ParticlesBg
  },
  data() {
    return {
      loginView: true,
      registerView: false,
      forgotPasswordView: false,
    };
  },
  methods: {
    changeCard(event) {
      switch (event) {
        case "register":
          this.loginView = false;
          this.registerView = true;
          this.forgotPasswordView = false;
          break;
        case "forgot_password":
          this.loginView = false;
          this.registerView = false;
          this.forgotPasswordView = true;
          break;
        default:
          this.loginView = true;
          this.registerView = false;
          this.forgotPasswordView = false;
          break;
      }
    },
  },
};
</script>
   
