import { mapActions, mapState } from "vuex";
// import VueJwtDecode from "vue-jwt-decode";
export default {
    methods: {
        ...mapActions("User", ["ActionSetUser", "ActionSetTheme"]),
        async logIn(form) {
            this.$refs.email.validate();
            this.$refs.password.validate();
            try {
                const { data } = await this.$http.post("accounts/login", form);
                if (data) localStorage.setItem("baseToken", data);
                this.$router.push({ name: "Dashboard" });
                // if (res.data.token) {
                //     let tokenDecode = VueJwtDecode.decode(res.data.token);
                //     if (tokenDecode) {
                //         let user = {
                //             id: tokenDecode.id,
                //             email: tokenDecode.email,
                //             name: tokenDecode.name,
                //             theme: tokenDecode.theme,
                //         };
                //         localStorage.setItem("baseUser", JSON.stringify(user));
                //         this.ActionSetUser(user);
                //     }
                //     this.$router.push({ name: "Dashboard" });
                // }
            } catch (error) {
                console.log(error);
                if (error.response) {
                    console.log(
                        `Servidor retorno um erro: ${error.message} ${error.response.statusText}`
                    );
                }
            }
        },
        async logOut() {
            try {
                let res = await this.$http.post("/accounts/logout");
                if (res.status == 200) {
                    localStorage.removeItem("baseToken");
                    localStorage.removeItem("baseUser");
                    this.$router.push("/authentication");
                    this.ActionSetUser("");
                    this.ActionSetTheme("light");
                }
            } catch (error) {
                console.log(error);
            }
        },

        async registerUser(form) {
            form.type = JSON.stringify(form.type); // convertendo json em string
            this.$q.loading.show(); // abrindo o loading
            try {
                let { data } = await this.$http.post("/user", form);
                console.log(data);
                this.$emit("change");
            } catch (error) {
                console.log(error);
            } finally {
                this.$q.loading.hide();
            }
        },

        // setData() {
        //     let user = JSON.parse(localStorage.getItem("baseUser"));
        //     this.ActionSetUser(user ? user : {});
        //     this.ActionSetTheme(user ? user.theme : "light");
        // },
        setTheme() {
            if (this.theme == "dark") this.$q.dark.set(true);
            else this.$q.dark.set(false);
        },
    },
    computed: {
        ...mapState("User", ["user", "theme"]),
        isMobile() {
            return ((this.$q.platform.is.mobile || this.$q.screen.name == 'xs') && this.$q.screen.height > this.$q.screen.width)
        }
    },
};